.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}

.modal-content {
  border: 1px solid #aaa;
  border-radius: 5px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 500px;
  min-height: 250px;
  display: flex;
  flex-direction: column;
  transition: all 300ms ease-in-out;
}

.modal-header,
.modal-body,
.modal-footer {
  padding: 20px;
}

.modal-header + .modal-body {
  border-top: 1px solid #aaa;
}

.modal-header + .modal-footer,
.modal-body + .modal-footer {
  border-top: 1px solid #aaa;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header .close {
  font-size: 1.2rem;
  cursor: pointer;
}

.modal-body {
  flex: 1;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
}

.modal-backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
}

.btn {
  width: 100px;
  padding: 12px 20px;
  font-size: 1rem;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.btn-cancel {
  background-color: #6c757d;
  color: #fff;
}

.btn-submit {
  background-color: #0d6efd;
  color: #fff;
}

.modal-footer .btn + .btn {
  margin-left: 10px;
}
